
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Search from 'vux/src/components/search/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import { getStore, setStore } from '../../config/mUtils'
import { getHot, getSearch } from '../../service/getData'
const list = () => ['', '']

export default {
components: {
Flexbox,
FlexboxItem,
Search,
XImg,
Scroller,
Divider
},
mounted() {
this.$vux.loading.show({
text: '...'
})
this.historyData = JSON.parse(getStore('historyData')) || []
if(this.historyData.length===0){
this.isHistory=false
}

getHot().then(resolved => {

this.hot = resolved
})
this.$vux.loading.hide()
var screenHeight = screen.height;

},
methods: {
del() {
this.isHistory = false
setStore('historyData',[])
},
see() {
this.isshow = !this.isshow
},
setFocus() {
this.$refs.search.setFocus()
},
hotSubmit(name){
this.value = name
this.setFocus()
},
resultClick(item) {

console.log(item.title)
this.value = item.title
},
getResult(val) {
var pattern = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>/?~@#&*|{}']")
if(pattern.test(this.value)){
this.search=false
return this.$vux.toast.show({
text:'',
type:'warn',
width:'14em'
})
}else{
this.search = true
}
this.results = val ? getResult(this.value) : []
},
onSubmit() {


if(this.search===false)
return
getSearch(1,this.value.trim()).then(resolved => {
console.log(JSON.stringify(resolved))
this.goodList = resolved.data
this.iscontent = true
})
this.$refs.search.setBlur()
this.historyData.forEach(function(element) {
if(this.historyData.indexOf(this.value) === -1){
this.historyData.push(this.value);
setStore('historyData',JSON.stringify(this.historyData))
}
}, this);
if(this.historyData.length===0){
this.historyData.push(this.value);
setStore('historyData',JSON.stringify(this.historyData))
}



},
onFocus() {
const height = screen.height;
this.issearch = true
document.getElementsByClassName('vux-search_show')[0].style.height = 0 + 'px';
},
onCancel() {
if(this.historyData.length!=0){
this.isHistory = true
this.historyData = JSON.parse(getStore('historyData') || "[]")
}
this.issearch = false
this.goodList = []
},
openDetail(index) {
console.log(index)
this.$router.push({
name: 'Commodity',
query: {
id: index
}
});
},
loadMore() { //
setTimeout(() => {
this.$nextTick(() => {
this.$refs.scroller.reset()
this.$vux.loading.hide()
})
this.$refs.scroller.donePullup()
}, 2000)
},
refresh() { //
setTimeout(() => {
this.$nextTick(() => {
this.$vux.loading.hide()
this.$nextTick(() => {
this.$refs.scroller.reset()
this.$vux.loading.hide()
this.$refs.scroller.donePulldown()
this.pullupEnabled && this.$refs.scroller.enablePullup()
})
})
}, 2000)
},
success(src, ele) {
const span = ele.parentNode.querySelector('span')
ele.parentNode.removeChild(span)
},
error(src, ele, msg) {
console.log('error load', msg, src)
const span = ele.parentNode.querySelector('span')
span.innerText = ''
},
},
data() {
return {
title: '',
results: [],
hot:[],
goodList:[],
value: '',
historyData:[],
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
isHistory: true,
isshow: true,
issearch:false,
search:true,
iscontent:false,
pulldown:{
content: '',
height: 60,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: '...',
clsPrefix: 'xs-plugin-pulldown-'
},
pullup:{
content: '',
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: '...',
clsPrefix: 'xs-plugin-pullup-'
}
}
}
}

function getResult(val) {








}



